function ajax(requestData, requestUrl, requestMethod, successFunc, errorFunc) {
  var data = null
  if(requestData) {
    data = JSON.stringify(requestData);
  }
  $.ajax({
    data,
    url: '/' + requestUrl,
    type: requestMethod,
    dataType: "json",
    contentType: 'application/json; charset=utf-8',
    success: function (e) {
      successFunc(e)
    },
    error: function(e) {
      errorFunc(e)
    },
  });
}
