// 链接跳转页面
function linkToPage(e) {
  var href = $(e).attr('href')
  if(href[0] === '/') {
    location.href = href
  } else if(href) {
    window.open(href);
  }
  return false;
}

// 按钮跳转页面
function buttonToPage(page) {
  var href = '/' + getLanguage() + page
  if(page === '/contact') {
    window.open(href)
  } else {
    location.href = href
  }
}

// 转换ID选择器为类选择器
function changeIdSelectorToClassSelector(idSelector) {
  return '.' + idSelector.substr(1)
}

// 跳转至单点登录
function toSso(page) {
  location.href = page + '?redirect_uri=' + location.href
}

// 跳转至3D设计
function toDesign(page) {
  window.open(page)
}
