class Cookie {
  constructor(attributes = {}) {
    this.attributes = attributes
  }

  extend(...args) {
    const result = {};
    for (let i = 0; i < args.length; i++) {
      const attributes = args[ i ]
      for (let key in attributes) {
        result[key] = attributes[key]
      }
    }
    return result;
  }

  decode(s) {
    return s.replace(/(%[0-9A-Z]{2})+/g, decodeURIComponent)
  }

  /**
   * 获取Cookie值
   * @param  {String} key  关键词
   * @param  {Boolean} json 是否转换JSON
   * @return {String/Object}      值
   */
  get(key, json) {
    if (typeof document === 'undefined') {
      return;
    }

    const jar = {}
    const cookies = document.cookie ? document.cookie.split('; ') : []
    for (let i = 0; i < cookies.length; i++) {
      const parts = cookies[i].split('=');
      let cookie = parts.slice(1).join('=');

      if (!json && cookie.charAt(0) === '"') {
        cookie = cookie.slice(1, -1);
      }

      try {
        const name = this.decode(parts[0])
        if (json) {
          try {
            cookie = JSON.parse(cookie)
          } catch (e) {}
        }

        jar[name] = cookie;

        if (key === name) {
          break;
        }
      } catch (e) {}
    }
    return key ? jar[key] : jar
  }

  /**
   * 设置Cookie
   * @param {String} key        关键词
   * @param {String} value      值
   * @param {Object} attributes 属性设置
   * @return {String}           Cookie
   */
  set(key, value, attributes) {
    attributes = this.extend({
      path: '/'
    }, this.attributes, attributes)
    if (typeof attributes.expires === 'number') {
      attributes.expires = new Date(new Date() * 1 + attributes.expires * 864e+5)   //  864e+5是一天的毫秒数
    }

    attributes.expires = attributes.expires ? attributes.expires.toUTCString() : ''

    try {
      const result = JSON.stringify(value);
      if (/^[\{\[]/.test(result)) {
        value = result
      }
    } catch (e) {}

    let stringifiedAttributes = ''
    for(let attributeName in attributes) {
      if (!attributes[attributeName]) {
        continue
      }
      stringifiedAttributes += '; ' + attributeName;
      if (attributes[attributeName] === true) {
        continue
      }
      stringifiedAttributes += '=' + attributes[attributeName].split(';')[0]
    }
    return (document.cookie = key + '=' + value + stringifiedAttributes)
  }

  /**
   * 移除Cookie值
   * @param  {String} key        关键词
   * @param  {Object} attributes 属性设置
   * @return {String}            Cookie
   */
  remove(key, attributes) {
    return this.set(key, '', this.extend(this.attributes, attributes, { expires: -1 }))
  }
}

window.cookie = new Cookie({ domain: '.aihouse.com' })